.todo {
  display: flex;
  justify-content: space-between;
  background-color: #ede5da;
  width: 80%;
  height: 80%;
  margin: 40px 40px 0;
  padding: 30px 60px;
  border-radius: 50px;
  font-weight: 600;

  &:last-child {
    margin-bottom: 40px;
  }
}

.done {
  color: green;
}

.inProgress {
  color: red;
}
